import InvoiceItem from './InvoiceItem';
export default {
  name: 'InvoiceItemList',
  components: {
    InvoiceItem: InvoiceItem
  },
  data: function data() {
    return {
      rows: [{
        title: '',
        quantity: 0,
        description: '',
        totalPrice: 0,
        fee: 0
      }],
      sumOfItems: 0
    };
  },
  mounted: function mounted() {
    this.$watch('rows', function () {
      this.sumOfItems = this.calculateSum();
    }, {
      deep: true
    });
  },
  methods: {
    addItem: function addItem() {
      this.rows.push({
        title: '',
        quantity: 0,
        description: '',
        totalPrice: 0,
        fee: 0
      });
    },
    calculateSum: function calculateSum() {
      return this.rows.reduce(function (n, item) {
        return n + item.totalPrice;
      }, 0);
    },
    rowDataChanged: function rowDataChanged(param) {
      this.sumOfItems = this.calculateSum();
      this.rows[param.index] = param.data;
      this.emitResult();
    },
    rowRemoved: function rowRemoved(rowIndex) {
      if (this.rows.length > 1) {
        this.rows.splice(rowIndex, 1);
        this.sumOfItems = this.calculateSum();
        this.emitResult();
      }
    },
    emitResult: function emitResult() {
      var result = {
        items: this.rows,
        total: this.sumOfItems
      };
      this.$emit('input', result);
    }
  }
};